.button {
	border: 1.5px solid rgb(118, 202, 231);
	padding: 10px;
	text-align: center;
	margin: 0 5px;
	font-weight: 700;
	color: rgb(118, 202, 231);
	cursor: pointer;
	transition: all 0.5s ease;
	text-transform: uppercase;
}

.button:hover {
	background-color: rgb(118, 202, 231);
	color: #fff;
}
